.container {
    position: relative;
    height: 90%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10rem;

    @media screen and (max-width: 760px) {
        padding: 0 8rem;
    }

    @media screen and (max-width: 760px) {
        padding: 0 1rem;
    }

}

.imgWrapper {
    display: block;
    position: absolute;
    bottom: 0;
    right: 5%;
    width: 40%;
    height: 90%;

    @media screen and (max-width: 768px) {
        width: 70%;
    }

    @media screen and (max-width: 430px) {
        display: none;
    }
}

.contentWrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;

    h3 {
        font-size: 2em;
    }

    h1 {
        font-size: 10em;
    }

    h2 {
        font-size: 1.7em;
    }

    @media screen and (max-width: 760px) {
        h3 {
            font-size: 1em;
        }
    
        h1 {
            font-size: 5em;
        }
    
        h2 {
            font-size: 0.76em;
        }
    }
}